html {
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;  
}
.menu {
    background-color: #24C0EB;
    border: 2px solid black;
    width: 100%;
    display: inline-flex;
    justify-content: space-around;
    align-items: center;
}

.menu a {
    text-decoration: none;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: black;
    padding: 10px;
}